#nav {width: 100%; margin: 0 auto;border-bottom: 1px solid #ddd; height: 100px;}
#nav nav {width: 100%; text-align: center;padding: 40px 0;}
#nav nav .nav-box {display: inline-block;}
#nav nav .nav-box ul {display: flex;}
#nav nav .nav-box ul li {margin: 0 20px;}
#nav nav .nav-box ul li a {text-transform: uppercase; font-size: 20px; color: #333; font-weight: 500; transition: all .3s;}
#nav nav .nav-box ul li a:hover {color: #c44;}


.main {width: 1280px; margin: 0 auto; padding: 40px 0 0 0;}
.main .img {width: 100%;position: relative;}
.main .img::before {content: '';position: absolute;top: 0;left: 0;width: 100%;height: 150%;font-size: 150px;font-weight: 800;color: rgba(50, 200, 250, .5);display: flex;justify-content: center;align-items: center;font-family: 'Montserrat';}

.main1 .img::before {content: 'MAIN1';}
.main2 .img::before {content: 'MAIN2';}
.main3 .img::before {content: 'MAIN3';}
.main4 .img::before {content: 'MAIN4';}
.main5 .img::before {content: 'MAIN5';}
.main6 .img::before {content: 'MAIN6';}

.main .img img {width: 100%;}

